// Theme variables.

$width-page: 1400px;

// Site Colors.
$color-body: #575756;
$color-heading: #293749;
$color-white: #FFFFFF;
$color-neutral-1: #4a4a4a;
$color-neutral-2: #81a5bf;
$color-neutral-3: #ddd;
$color-neutral-4: #5f8dae;
$color-neutral-5: #ccc;
$color-neutral-6: #f5f5f5;
$color-neutral-7: #212121;
$color-neutral-8: #666;
$color-neutral-9: #333;
$color-neutral-10: #e6e5e5;
$color-neutral-11: #adadad;
$color-neutral-12: #7099b6;
$color-neutral-13: #4f7b9b;
$color-neutral-14: #222;
$color-neutral-15: #eee;
$color-neutral-16: #414a51;
$color-off-white: #FEFDFA;
$color-black-rgba-30: rgba(0, 0, 0, 0.30);

// Bootstrap global overrides.
$bs-breakpoint-xs: 0;
$bs-breakpoint-sm: 576px;
$bs-breakpoint-md: 768px;
$bs-breakpoint-lg: 992px;
$bs-breakpoint-xl: 1200px;
$bs-breakpoint-xxl: 1400px;

// Fonts.
$font-main: "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;
$font-heading: "Roboto Condensed", Verdana, Arial, Helvetica, sans-serif;
//$font-accent: var( --kwall-font-accent);
